var convertData = function(data) {
  var geoCoordMap = new Object()
  data.forEach(function(item) {
    item.companyName = item.companyName || item.name
    item.name = item.companyName
    item.typeStr = item.typeStr || 1 // 1
    geoCoordMap[item.companyName] = [item.longitude, item.latitude]
  })
  var res = [],
    color = '#06CAFF'
  for (var i = 0; i < data.length; i++) {
    var geoCoord = geoCoordMap[data[i].companyName]
    // 1- 2- 3-
    if (data[i].typeStr == 1) {
      if (data[i].level == '') {
        color = '#F600FF'
      } else if (data[i].level == '') {
        color = '#2AF80F'
      }
    } else if (data[i].typeStr === 3) {
      color = 'green'
    }
    // else if(typeStr==2){
    //     var color='#8378ea';
    // }else{
    //     var color='#74d7db';
    // };
    if (geoCoord) {
      res.push({
        name: data[i].companyName,
        value: geoCoord.concat(data[i].warningNum),
        warningNum: data[i].warningNum,
        level: data[i].level,
        finalScore: data[i].riskScore,
        typeStr: data[i].typeStr,
        link: data[i].link,
        addr: data[i].addr,
        img: data[i].img,
        healthDegree: data[i].finalScore,
        // riskWaringLevel:data[i].riskWaringLevel,
        // riskWaringCount:data[i].riskWaringCount,
        // newsCount:data[i].newsCount,
        itemStyle: {
          normal: {
            color,
            // color: "#f2b442",
            shadowBlur: 10,
            shadowColor: '#333'
          }
        }
      })
    }
  }
  return res
}
